.popup {
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;

  transition: visibility 0.15s, opacity 0.3s linear;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
}