.profile {
  display: grid;
  grid-template-columns: 120px calc(100% - 120px - 150px - 30px*2) 150px;
  gap: 30px;

  margin-bottom: 50px;
}

@media screen and (max-width: 767px) {
  .profile {
    grid-template-columns: 100%;
    justify-items: center;

    margin-bottom: 36px;
  }
}